<template>
	<div>
		<!--<mu-raised-button label="" @click="open('top')" />
		<mu-raised-button label="" @click="open('bottom')" />
		<mu-raised-button label="" @click="open('left')" />
		<mu-raised-button label="" @click="open('right')" />-->
		<mu-popup position="bottom" popupClass="demo-popup-bottom" :open="bottomPopup" @close="close('bottom')">
			<mu-appbar title="">
				<mu-flat-button slot="right" label="" color="white" @click="close('bottom')" />
			</mu-appbar>
			<mu-content-block>
				<p>
					Popup 
				</p>
				<p>
					
				</p>
			</mu-content-block>
		</mu-popup>
		<mu-popup position="top" :overlay="false" popupClass="demo-popup-top" :open="topPopup">
			
		</mu-popup>
		<mu-popup position="left" popupClass="demo-popup-left" :open="leftPopup" @close="close('left')">
			<mu-raised-button label="" @click="close('left')" primary fullWidth/>
		</mu-popup>
		<mu-popup position="right" popupClass="demo-popup-right" :open="rightPopup" @close="close('right')">
			<mu-raised-button label="" @click="close('right')" primary fullWidth/>
		</mu-popup>
	</div>
</template>

<script>
	export default {
		data() {
				return {
					//bottomPopup: false,
					topPopup: false,
					//leftPopup: false,
					rightPopup: false
				}
			},
			computed:{
				leftPopup:function(){
					return this.$store.state.isLeftPopUp
				},
				bottomPopup:function(){
					return this.$store.state.isBottomPopUp
				}
			},
			methods: {
				open(position) {
					this[position + 'Popup'] = true
				},
				close(position) {
					if(position=="left"){
						this.$store.commit("set_isLeftPopUp",false)
					}else if(position=="bottom"){
						this.$store.commit("set_isBottomPopUp",false)
					}else{
						this[position + 'Popup'] = false
					}
				}
			},
			watch: {
				topPopup(val) {
					if(val) {
						setTimeout(() => {
							this.topPopup = false
						}, 2000)
					}
				}
			}
	}
</script>

<style lang="css">
	.demo-popup-top {
		width: 100%;
		opacity: .8;
		height: 48px;
		line-height: 48px;
		display: flex;
		align-items: center;
		justify-content: center;
		max-width: 375px;
	}
	
	.demo-popup-left {
		display: flex;
		width: 85%;
		max-width: 375px;
		height: 100%;
		align-items: center;
		padding: 24px;
	}
	
	.demo-popup-right {
		display: flex;
		width: 100%;
		height: 100%;
		max-width: 375px;
		align-items: center;
		padding: 24px;
	}
	
	.demo-popup-bottom {
		width: 100%;
		max-width: 375px;
	}
</style>


